@import url(http://fonts.cdnfonts.com/css/sf-ui-display);
* {
	color: #ffffff;
	font-family: 'SF UI Display', sans-serif;
}

body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: #e5e5e5;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

a {
	text-decoration: none;
}

.show {
	display: block !important;
}

.no-show {
	display: none !important;
}

input {
	outline: none;
}

@media (max-width: 377px) {
	.header-search input {
		font-size: 20px;
	}

	.header-title-favorite span {
		display: none;
	}

	.header-nav {
		display: none;
	}

	.header-title-logo span {
		display: none;
	}
}

.header {
	height: 400px;
	width: 100%;
	background-color: black;
}

.header-title {
	padding-top: 90px;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
}

.header-title-logo {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
}

.header-title-logo span {
	font-size: 24px;
	margin-left: 24px;
}

.header-title-favorite {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
}
.header-title-favorite img {
	margin-left: 20px;
}
.header-title-favorite span {
	margin-left: 10px;
}

.header-search {
	margin-top: 40px;
	font-size: 72px;
	text-align: center;
}

.header-search input {
	background-color: black;
	border: none;
	text-align: center;
	color: #fff;
	border-bottom: solid 1px #fff;
}

.header-nav {
	margin-top: 30px;
}

.header-nav-list {
	list-style: none;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
}

.header-nav-item {
	font-size: 18px;
	font-weight: 200;
}

.btn-view {
	height: 150px;
	text-align: center;
}

.btn-view img {
	margin: 70px 10px;
}

.btn-view img:hover {
	cursor: pointer;
}

.pictures-view-list {
	text-align: center;
	margin: 10px auto;
	border-radius: 8px;
}

.picture-list {
	width: 1080px;
	text-align: center;
	margin: 10px auto;
	position: relative;
	border-radius: 8px;
}
.picture-list img {
	border-radius: 8px;
}

.picture-list-back {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	background-color: rgba(0, 0, 0, 0.7);
	border-radius: 8px;
	color: white;
}

.picture-view-item-back-inner-icons {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	margin-top: 100px;
	width: 250px;
}

.picture-list-back-description {
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}

.picture-view-item-back-inner-name {
	margin-top: 13px;
	font-size: 30px;
	color: #ffffff;
}

.pictures-view-item-img {
	margin: 10px auto;
	position: relative;
	border-radius: 8px;
}

.pictures-view-item-img img {
	border-radius: 8px;
}
.pictures-view-item-back {
	background-color: rgba(0, 0, 0, 0.7);
	position: absolute;
	top: 0;
	height: 100%;
	width: 96%;
	border-radius: 8px;
}

.pictures-view-item-back-descr {
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	text-align: center;
}

.picture-view-item-back-inner-icons2 {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	margin-top: 45px;
	width: 250px;
}

.row {
	-webkit-align-items: center;
	        align-items: center;
}

.picture-view-item-back-inner-icons2 img {
	width: 35px;
	height: 35px;
}

.header-small {
	height: 80px;
	background-color: black;
}

.header-small-wrapper {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	margin-top: 26px;
}

.header-small-logo {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
}

.header-small-logo span {
	font-size: 24px;
	margin-left: 18px;
}

.header-small-favorite {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
}

.header-small-favorite img {
	margin-left: 40px;
}

.header-small-favorite span {
	font-size: 20px;
	line-height: 24px;
	margin-left: 10px;
}

.header-small-favorite input {
	color: black;
	outline: none;
}

.title {
	margin-top: 110px;
	text-align: center;
}

.title span {
	font-size: 72px;
	font-weight: 700;
	line-height: 86px;
	color: #000000;
}

.picture-main {
	height: auto;
	padding-bottom: 40px;
}

.picture-main-title {
	margin-top: 46px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
}

.picture-main-title-author {
	display: -webkit-flex;
	display: flex;
	-webkit-align-content: center;
	        align-content: center;
}
.picture-main-title-author img {
	width: 56px;
	height: 56px;
	border-radius: 8px;
}

.picture-main-title-author-name {
	margin-left: 10px;
}

.picture-main-title-author-name-first-last {
