}

.subtitle-wrapper {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: flex-end;
	        align-items: flex-end;
	margin-top: 80px;
	margin-bottom: 47px;
}

.subtitle-main {
